<template>
	<view style="display: flex;justify-content: center;align-items: center;height: 100vh;">
		<el-card v-bind:style="{width: widths}">
			<div slot="header" class="clearfix">
				<span><b>{{$t('enroll.Registered')}}</b></span>
			</div>
			<el-form ref="ruleForm" :rules="rules" :model="ruleForm" :label-width="labelWidth" :label-position="labelPosition" >
				<el-form-item :label="$t('NickName')+':'">
					<el-input v-model="ruleForm.nickname" ></el-input>
				</el-form-item>
				<el-form-item :label="$t('login.eamil')+':'" required prop="email">
					<el-input v-model="ruleForm.email" :placeholder="$t('withdrawal.fillemail')"></el-input>
				</el-form-item>
				<el-form-item>
					<!-- <el-input v-model="ruleForm.eamils" :placeholder="$t('login.eamil')"></el-input> -->
					<el-button style="float: right;" type="text" @click="handleEamil"><b><i
								class="el-icon-thumb"></i> {{$t('home.Verifymailbox')}}</b></el-button>
				</el-form-item>
				<el-form-item :label="$t('home.Emailcode')+':'" prop="eamilVerifycode">
					<el-input v-model="ruleForm.eamilVerifycode"></el-input>
				</el-form-item>
				<el-form-item :label="$t('login.password')+':'" required prop="password">
					<el-input v-model="ruleForm.password" :placeholder="$t('pass.enteryourpassword')"></el-input>
				</el-form-item>
				<el-form-item :label="$t('reg.Referrer')">
					<el-input v-model="ruleForm.Referrerid"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitForm('ruleForm')">{{$t('index.Registernow')}}</el-button>
					<el-button @tap="retrunLogin">{{$t('index.login')}}</el-button>
				</el-form-item>
			</el-form>
		</el-card>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				widths: '45%',
				labelPosition: 'right',
				ruleForm: {
					nickname: '',
					email: '',
					password: '',
					Referrerid: '',
					eamilVerifycode:''
				},
				rules: {
					email: [{
						required: true,
						message: this.$t('withdrawal.fillemail'),
						trigger: 'blur'
					}, ],
					password: [{
						required: true,
						message: this.$t('pass.enteryourpassword'),
						trigger: 'blur'
					}, ],
					eamilVerifycode:[{
						required: true,
						message: this.$t('pass.emailverificationcode'),
						trigger: 'blur'
					}]
				}
			}
		},
		computed:{
			labelWidth(){
				if(uni.getLocale()=='en'){
					return '180px';
				}else{
					return '100px';
				}
			}
		},
		mounted() {
			uni.setNavigationBarTitle({
				title: uni.getStorageSync('name')
			})
			this.getScreenWidth(); // 初始化获取屏幕宽度和缩放比例
			window.addEventListener('resize', this.handleResize); // 监听窗口大小变化
		},
		methods: {
			submitForm(ruleForm) {
				this.$refs[ruleForm].validate((valid) => {
					if (valid) {
						alert('submit!');
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			retrunLogin() {
				uni.navigateTo({
					url: '/pages/userLogin/userLogin'
				})
			},
			getScreenWidth() {
				this.screenWidth = window.innerWidth;
				if (this.screenWidth <= 990) {
					this.widths = '90%'
					this.labelPosition = 'top'
					// this.pagercounts= 2
				} else {
					this.widths = "45%"
					this.labelPosition = 'right'
					// this.pagercounts= 8
				}
			},
			handleResize() {
				const newScreenWidth = window.innerWidth;
				if (newScreenWidth !== this.screenWidth) {
					this.screenWidth = newScreenWidth;
					console.log(newScreenWidth);
					if (newScreenWidth <= 990) {
						this.widths = '90%'
						this.labelPosition = 'top'
						// this.pagercounts= 2
					} else {
						this.widths = "45%"
						this.labelPosition = 'right'
						// this.pagercounts= 8
					}
				}
			},
		}
	}
</script>